@tablet-width: 768px;
@desktop-width: 1200px;
@mobile-width-only: 767px;

.living-example {
  margin: 0 auto;
  width: 320px;
  background: #f2f2f2;
}

@media (min-width: @tablet-width) {
  .living-example {
    margin: 0 auto;
    width: 768px;
    background: #f2f2f2;
  }
}

@media (min-width: @desktop-width) {
  .living-example {
    margin: 0 auto;
    width: 1200px;
    background: #f2f2f2;
  }
}


.living-example__title {
  font-family: "Oswald", sans-serif;
  font-style: normal;
  font-weight: 400;
  font-size: 36px;
  line-height: 40px;
  color: #000000;
}

@media (min-width: @tablet-width) {
  .living-example__title {
    font-family: "Oswald", sans-serif;
    font-style: normal;
    font-weight: 400;
    font-size: 60px;
    line-height: 60px;
    color: #000000;
  }
}

@media (min-width: @desktop-width) {
  .living-example__title {
    font-family: "Oswald", sans-serif;
    font-style: normal;
    font-weight: 400;
    font-size: 60px;
    line-height: 60px;
    color: #000000;
  }
}

.living-example__tetx {
  font-family: "Arial", sans-serif;
  font-style: normal;
  font-weight: 400;
  font-size: 14px;
  line-height: 18px;
  color: #444444;
}

@media (min-width: @tablet-width) {
  .living-example__tetx {
    font-family: "Arial", sans-serif;
    font-style: normal;
    font-weight: 400;
    font-size: 16px;
    line-height: 24px;
    color: #444444;
  }
}

@media (min-width: @desktop-width) {
  .living-example__tetx {
    font-family: "Arial", sans-serif;
    font-style: normal;
    font-weight: 400;
    font-size: 16px;
    line-height: 24px;
    color: #444444;
  }
}

.living-example__result {
  font-family: "Oswald", sans-serif;
  font-style: normal;
  font-weight: 400;
  font-size: 30px;
  line-height: 37px;
  color: #000000;
}

.living-example__days-spent {
  font-family: "Oswald", sans-serif;
  font-style: normal;
  font-weight: 400;
  font-size: 30px;
  line-height: 37px;
  color: #000000;
}

.living-example__intro {
  font-family: "Arial", sans-serif;
  font-style: normal;
  font-weight: 400;
  font-size: 14px;
  line-height: 14px;
  color: #444444;
}

.living-example__position {
  display: flex;
  flex-wrap: wrap;
}

.living-example__wrapper {
  margin: 0 auto;
}

.living-example__spending {
  font-family: "Oswald", sans-serif;
  font-style: normal;
  font-weight: 400;
  font-size: 20px;
  line-height: 20px;
  text-align: center;
  text-transform: uppercase;
  color: #000000;
}
